import React from 'react';
import { Checkbox, Form } from 'antd';
import { getInitJson, getTools } from './utils';
import Config from './config';
import { FORMITEM_LAYOUT } from 'Src/utils/constants';

interface Props {
    config: {
        options: [{
            props: {
                disabled: boolean;
                checked: boolean;
                value: string;
            };
            text: string;
            rowKey: number;
        }];
        label: string;
        key: string;
    };
}

class KCheckBox extends React.Component<Props> {

    render() {
        const { config } = this.props;
        return (
            <Form.Item
                label={config && config.label ? config.label : ''}
                {...FORMITEM_LAYOUT}
                style={{ marginBottom: 0 }}
            >
                {
                    config.label
                        ? config.options.map((ele, index) => {
                            return <Checkbox
                                key={index}
                                checked={ele.props.checked}
                                disabled={ele.props.disabled}
                            >{ele.text}</Checkbox>;
                        })
                        : <Checkbox
                            checked={false}
                            disabled={false}
                        ></Checkbox>
                }
            </Form.Item>
        );
    }
}

export {
    KCheckBox as component,
    getInitJson,
    getTools,
    Config as config
};
