import React, { Component } from 'react';
import { Input, Button, Row, Col, Icon, Switch, Alert, Table } from 'antd';
import PropTypes from 'prop-types';
import Form from 'antd/es/form';
import { findComponent, saveComponent } from 'Src/utils';
import { FORMITEM_LAYOUT, ALIAS } from 'Src/utils/constants';

const VALUE = 'value';
const LABEL = 'label';
const TEXT = 'text';
const CHECK = 'checked';
const DISABLED = 'disabled';
const OPTIONS = 'options';
const KEY = 'key';
const ROW_KEY = 'rowKey';

interface CheckBoxConfigProps {
    pageJSON: any;
    onSave(pageJSON: any): void;
}

export default class CheckBoxConfig extends Component<CheckBoxConfigProps> {
    static propTypes = {
        onSave: PropTypes.func
    };

    state = {
        formData: {
            [OPTIONS]: [{
                props: {
                    [DISABLED]: false,
                    [CHECK]: false,
                    [VALUE]: ''
                },
                [TEXT]: '',
                [ROW_KEY]: 0
            }],
            [LABEL]: '',
            [KEY]: '',
        },
        isTouch: false,
        errMessage: '',
        current: {
            id: '',
            props: {}
        }
    };

    columns = [
        {
            title: 'Key',
            dataIndex: 'value',
            key: 'value',
            render: (item, record, index) => <Input
                value={record.props[VALUE]}
                placeholder=' '
                onChange={this.handleChange.bind(this, VALUE, index)}
            />
        },
        {
            title: '',
            dataIndex: 'text',
            key: 'text',
            render: (item, record, index) => <Input
                value={record[TEXT]}
                placeholder=' '
                onChange={this.handleChange.bind(this, TEXT, index)}
            />
        },
        {
            title: '',
            dataIndex: 'check',
            key: 'check',
            render: (item, record, index) =>
                <Switch
                    defaultChecked={record[CHECK]}
                    checkedChildren=""
                    unCheckedChildren=""
                    onChange={this.handleChange.bind(this, CHECK, index)}
                />
        },
        {
            title: '',
            dataIndex: 'disabled',
            key: 'disabled',
            render: (item, record, index) =>
                <Switch
                    defaultChecked={record[DISABLED]}
                    checkedChildren=""
                    unCheckedChildren=""
                    onChange={this.handleChange.bind(this, DISABLED, index)}
                />
        },
        {
            title: '',
            dataIndex: 'delete',
            key: 'delete',
            render: (text, record, index) =>
                this.state.formData.options.length > 1
                    ? <Col>
                        <Icon type="close" onClick={() => { this.handleDeleteChekItem(index); }} />
                    </Col>
                    : <></>

        }
    ];

    static getDerivedStateFromProps(props, state) {
        if (!state.isTouch) {
            const { pageJSON } = props;
            const { components } = pageJSON;
            const current = findComponent(components);
            return {
                formData: {
                    [OPTIONS]: current.options || [{
                        props: {
                            [DISABLED]: false,
                            [CHECK]: false,
                            [VALUE]: ''
                        },
                        [TEXT]: '',
                        [ROW_KEY]: 0
                    }],
                    [LABEL]: current.label || '',
                    [KEY]: current.key || '',
                },
                current
            };
        } else {
            return state;
        }
    }

    /**
     * @desc 
     */
    handleSave = () => {
        const { formData, current } = this.state;
        const { pageJSON, onSave } = this.props;
        if (!formData.label) {
            this.setState({
                errMessage: ''
            });
            return;
        }
        for (let i = 0; i < formData.options.length; i++) {
            const item = formData.options[i];
            if (!item[TEXT]) {
                this.setState({
                    errMessage: ''
                });
                return;
            }
        }
        pageJSON.components = saveComponent(current.id, pageJSON.components, formData);
        onSave && onSave(pageJSON);
    }

    /**
     * @desc    
     */
    handleChange = (itemKey, index: number, e) => {
        const { formData } = this.state;
        const value = typeof e === 'boolean' ? e : e.target.value;
        switch (itemKey) {
            case LABEL:
                formData.label = value;
                break;
            case KEY:
                formData.key = value;
                break;
            case TEXT:
                formData.options[index][itemKey] = value;
                break;
            case CHECK:
            case DISABLED:
                formData.options[index].props[itemKey] = value;
                break;
            default:
                formData.options[index].props[itemKey] = value;
                break;
        }
        this.setState({
            formData,
            isTouch: true
        });
    };

    /**
     * @desc 
     */
    handleAddCheck = (): void => {
        const { formData } = this.state;
        formData.options.push({
            props: {
                [DISABLED]: false,
                [CHECK]: false,
                [VALUE]: ''
            },
            [TEXT]: '',
            [ROW_KEY]: formData.options[formData.options.length - 1][ROW_KEY] + 1
        });
        this.setState({
            formData,
            isTouch: true
        });
    }

    /**
     * @desc 
     */
    handleDeleteChekItem = (index) => {
        const { formData } = this.state;
        formData.options.splice(index, 1);
        this.setState({
            formData
        });
    }

    /**
     * @desc    
     */
    handleAlertClose = () => {
        this.setState({
            errMessage: ''
        });
    }

    render() {
        const { formData, errMessage } = this.state;
        return <>
            {
                errMessage
                    ? <Alert message={errMessage}
                        type='error'
                        closable
                        onClose={this.handleAlertClose}
                    ></Alert>
                    : null
            }
            <br />
            <Form.Item
                label={ALIAS.LABEL}
                {...FORMITEM_LAYOUT}
            >
                <Input
                    value={formData.label}
                    placeholder=' label'
                    onChange={this.handleChange.bind(this, LABEL, 0)}
                />
            </Form.Item>
            <Form.Item
                label={ALIAS.KEY}
                {...FORMITEM_LAYOUT}
            >
                <Input
                    value={formData.key}
                    placeholder=' key'
                    onChange={this.handleChange.bind(this, KEY, 0)}
                />
            </Form.Item>
            <Table rowKey="rowKey" dataSource={formData.options} columns={this.columns} bordered pagination={false} />
            <br />
            <Row type="flex" justify='space-between'>
                <Col>
                    <Button onClick={this.handleAddCheck} type='primary' ></Button>
                </Col>
                <Col>
                    <Button onClick={this.handleSave} type='primary' ></Button>
                </Col>
            </Row>
        </>;
    }
}
